import React, { Component } from 'react';
import './index.scss'
import data from './data.json'
import Child from './components/child'
// å¼•å…¥å¯¹è¯æ¡†ã€æŒ‰é’®ã€å¡ç‰‡ã€å…¨å±€æç¤ºã€åˆ†å‰²çº¿
import { Modal, Button, Card, message } from 'antd';
class Index extends Component {
    constructor() {
        super();
        this.state = {
            radioLike: "like",//å•é€‰æ¡†
            checkbox: [false, false, false, false, false],//å¤é€‰æ¡†
            textarea: '',//æ–‡æœ¬åŸŸ
            visible: false,//å¯¹è¯æ¡†
            checkboxTrue: [],//å–œæ¬¢çš„æ°´æœå…·ä½“åç§°
            checAll: false,//å…¨é€‰
            checRever: false//åé€‰
        }
        this.copyState = this.state
    }
    handleChange = ({ target }) => {
        if (target.name === "radioLike") {
            this.setState({
                [target.name]: target.value
            }, () => {
                this.setState({
                    checkbox: [false, false, false, false, false],
                    checAll: false,
                    checRever: false,
                    textarea: ''
                })
            })
        } else if (target.name === "checkbox") {
            const newCheckbox = [...this.state.checkbox]
            newCheckbox[target.value] = target.checked;
            this.setState({
                [target.name]: newCheckbox
            }, () => {
                this.monitorCheckbox()
                this.setState({
                    checRever: false
                })
            })
        } else if (target.name === "checAll") {
            this.setState({
                [target.name]: target.checked
            }, this.checAll())
        } else if (target.name === "checRever") {
            this.setState({
                [target.name]: target.checked
            }, this.checRever())
        } else {
            this.setState({
                [target.name]: target.value
            })
        }
    }
    //å…¨é€‰
    checAll = () => {
        if (!this.state.checAll) {
            this.setState({
                checkbox: [true, true, true, true, true]
            })
        } else {
            this.setState({
                checkbox: [false, false, false, false, false]
            })
        }
    }
    // åé€‰
    checRever = () => {
        let newChecRever = [];
        // å¾ªç¯éå†checkboxæ•°ç»„ï¼Œå¯¹æ¯ä¸€é¡¹è¿›è¡Œå–åï¼Œå†æ”¾åˆ°ä¸€ä¸ªæ–°æ•°ç»„é‡Œ
        for (let i = 0; i < this.state.checkbox.length; i++) {
            newChecRever.push(!this.state.checkbox[i])
        }
        this.setState({
            checkbox: newChecRever
        }, () => {
            this.monitorCheckbox()
        })
    }
    // ç›‘å¬checkboxæ•°ç»„
    monitorCheckbox = () => {

        let isChecAll = true;
        // å¾ªç¯éå†checkboxæ•°ç»„ï¼Œå¯¹æ¯ä¸€é¡¹è¿›è¡ŒåŒ¹é…ï¼Œå¦‚æœæœ‰ä¸€é¡¹ä¸ºfalseçš„ï¼Œå°±å°†isChecAllè®¾ç½®ä¸ºfalse
        for (let i = 0; i < this.state.checkbox.length; i++) {
            if (!this.state.checkbox[i]) {
                isChecAll = false
            }
        }
        if (isChecAll) {
            this.setState({
                checAll: true
            })
        } else {
            this.setState({
                checAll: false
            })
        }
    }
    // éªŒè¯
    veri = () => {
        const { radioLike, checkbox, textarea } = this.state;
        if (radioLike === "nolike" ? false : !checkbox.find(item => item)) return message.warning("è¯·é€‰æ‹©ä½ å–œæ¬¢çš„æ°´æœ");
        if (!textarea.trim() || textarea.length < 3) return message.warning("ç†ç”±ä¸èƒ½å°‘äºä¸‰ä¸ªå­—");
        this.showModal();

    }
    // è¡¨å•æäº¤äº‹ä»¶
    handleSubmit = (event) => {
        event.preventDefault()//å–æ¶ˆé»˜è®¤äº‹ä»¶
        this.veri();
    }
    // é‡ç½®è¡¨å•äº‹ä»¶
    reSetForm = () => {
        this.setState(this.copyState)
    }

    render() {
        const { radioLike, checkbox, textarea, checkboxTrue, checAll, checRever } = this.state
        const isLike = radioLike === "like" ? true : false;
        // ç¦ç”¨çŠ¶æ€
        const disabled = isLike ? false : true;
        // æ–‡æœ¬åŸŸéƒ¨åˆ†çš„é—®é¢˜
        const textitle = isLike ? "åƒæ°´æœçš„å¥½å¤„" : "ä¸å–œæ¬¢åƒæ°´æœçš„åŸå› "
        let isChecked;
        return (
            <>
                <form  >
                    {data.map(item => {
                        // switch åˆ¤æ–­è¦ä¼ å…¥çš„checkedå€¼
                        switch (item.type) {
                            case "radio":
                                isChecked = radioLike
                                break;
                            case "checkbox":
                                isChecked = checkbox
                                break;
                            default:
                                break;
                        }
                        return <div key={item.id} className="section-002">
                            <h3>{item.id}ã€{item.problem}ã€{item.type === "radio" ? 'å•é€‰' : "å¤šé€‰"}ã€‘</h3>
                            {
                                // å…¨é€‰å’Œåé€‰éƒ¨åˆ†
                                item.type === "checkbox" ?
                                    <div className="section-002-active">
                                        <label>
                                            <input
                                                type="checkbox"
                                                name="checAll"
                                                value={checAll}
                                                checked={checAll}
                                                onChange={this.handleChange}
                                                disabled={disabled}
                                            />å…¨é€‰
                                                </label>
                                        <label>
                                            <input
                                                type="checkbox"
                                                name="checRever"
                                                value={checRever}
                                                checked={checRever}
                                                onChange={this.handleChange}
                                                disabled={disabled} />åé€‰
                                                </label>
                                    </div>
                                    :
                                    ""
                            }
                            {/* è°ƒç”¨å­ç»„ä»¶ */}
                            <Child data={item} checked={isChecked} handlechange={this.handleChange} disabled={disabled} />
                        </div>
                    })}
                    {/* æ–‡æœ¬åŸŸéƒ¨åˆ† */}
                    <div className="section-002">
                        <h3>{data.length + 1}ã€{textitle}</h3>
                        <label>
                            <textarea type="textarea" name="textarea" value={textarea} onChange={this.handleChange} cols="40" rows="5"></textarea>
                        </label>

                    </div>
                    {/* æŒ‰é’®éƒ¨åˆ† */}
                    <label className="btns">
                        <Button type="primary" onClick={this.handleSubmit}>æäº¤</Button>
                        <Button onClick={this.reSetForm}>é‡ç½®</Button>
                    </label>
                </form>
                {/* å¯¹è¯æ¡† */}
                <Modal
                    title="é—®å·è°ƒæŸ¥ç»“æœ"
                    visible={this.state.visible}
                    onOk={this.handleOk}
                    onCancel={this.handleCancel}
                    cancelText="å–æ¶ˆ"
                    okText="ç¡®å®š"
                >
                    <Card title="ä½ æ˜¯å¦å–œæ¬¢æ°´æœ" >
                        <p>{isLike ? "å–œæ¬¢" : "ä¸å–œæ¬¢"}</p>
                    </Card>
                    {
                        isLike ? <Card title="ä½ å–œæ¬¢çš„æ°´æœï¼š" >
                            {
                                checkboxTrue.map((item, index) => {
                                    return <p key={index}>{item}</p>
                                })
                            }
                        </Card> : ""
                    }
                    <Card title={textitle} >
                        <p>{textarea}</p>
                    </Card>
                </Modal>
            </>
        );
    }
    // å¯¹è¯æ¡†æ˜¾ç¤º
    showModal = () => {
        this.setState({
            checkboxTrue: []
        })
        for (let i = 0; i < this.state.checkbox.length; i++) {
            if (this.state.checkbox[i]) {
                setTimeout(() => {
                    this.setState({
                        checkboxTrue: [...this.state.checkboxTrue, data[1].labels[i].label]
                    })
                }, 0);
            }
        }
        this.setState({
            visible: true,
        });
    };
    // ç‚¹å‡»å¯¹è¯æ¡†ç¡®å®šæŒ‰é’®
    handleOk = e => {
        this.setState({
            visible: false,
        });
    };
    // ç‚¹å‡»å¯¹è¯æ¡†å–æ¶ˆæŒ‰é’®
    handleCancel = e => {
        this.setState({
            visible: false,
        });
    };
}

export default Index;